* {
  box-sizing: border-box;
  text-decoration: none;
}

body {
  font-family: Arial;
  margin: 0;
}

header {
  padding: 60px;
  text-align: center;
  background: #13293d;
  color: #fff;
}

nav {
  display: flex;
  justify-content: flex-end;
  background-color: #0a343b;
}

nav a {
  padding: 14px 20px;
  text-decoration: none;
  text-align: center;
  color: rgb(83, 235, 83);
}

main {
  display: flex;
}

aside {
  flex: 30%;
  background-color: #b9c6ae;
  padding: 20px;
  flex-wrap: wrap;
}

section {
  flex: 70%;
  background-color: #fff;
  padding: 20px;
  color: black;
}

footer {
  padding: 20px;
  text-align: center;
  background: #13293d;
  color: #fff;
}

.sample-img {
  background-color: #fff;
  width: 100%;
  padding: 20px;
  height: 200px;
  border: 1px solid black;
}

.sample-img2 {
  background-color: #fff;
  width: 50%;
  padding: 20px;
  height: 200px;
  border: 1px solid black;
}
